<template>
  <a
    class="history-item"
    @click="handlePostHistory(info)"
  >
    <div class="icon-container">
      <img :src="websrc(info.weblogo)">
    </div>
    <div class="infoURL">{{info.infoContent}}</div>
  </a>
</template>

<script>
import global from '../../global/global'
import mediaQueries from '../../global/mediaQuery'
export default {
  name: 'HotItem',
  props: {
    info: {
      weblogo: String,
      infoURL: String,
      infoContent: String
    }
  },
  methods: {
    websrc (url) {
      return global.DEFAULT_URL + global.WEBS_LOGO + url
    },
    handlePostHistory (item) {
      if (mediaQueries()) {
        window.open(item.infoURL)
      } else {
        /* eslint-disable */
        var embed = plus.webview.create(item.infoURL, '', { top: '49px', bottom: '49px' });
        plus.webview.currentWebview().append(embed);
      }

    }
  }
}
</script>

<style scoped>
.infoURL {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-left: 1rem;
}
.history-item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #adadad;
  padding-bottom: 0.2rem;
  margin: 0.2rem;
  text-decoration: none;
  color: black;
}
.icon-container {
  width: 3rem;
  height: 0;
  padding-bottom: 3rem;
}
img {
  width: 3rem;
  height: 3rem;
}
</style>



