<template>
  <div>
    <h4>今日最热</h4>
    <hot-item
      v-for="item of hotSource"
      :key="item._id"
      :info="item"
    ></hot-item>
  </div>
</template>
<script>
import { getUserPrefer } from '../../../request/api'
import HotItem from '../../../components/HotItem/HotItem'

export default {
  name: 'SubBody',
  components: {
    HotItem
  },
  data () {
    return {
      hotSource: []
    }
  },
  created () {
    getUserPrefer(localStorage.username).then(result => {
      let hotSource = this.hotSource
      console.log(result)
      result.data.forEach(res => {
        for (let i = 0; i < res.length - 1; i++) {
          let temp = res[i]
          let web = res[res.length - 1]
          // 将数据格式化
          hotSource.push({
            _id: temp._id,
            weblogo: web.weblogo,
            infoURL: temp.infoURL,
            infoContent: temp.infoContent
          })
        }
      })
      console.log(hotSource)
    })
  }

}
</script>

<style scoped>
</style>



