<template>
  <div>
    <!-- 头部 -->
    <list-header>
      <template #content>
        所有标签
      </template>
    </list-header>
    <!-- 所有榜单 -->
    <div
      ref="listContant"
      class="wapper"
    >
      <div>
        <!-- 我的榜单 -->
        <list-body :sourceList="mineList">
          <div>我的榜单</div>
        </list-body>
        <!-- 热门列表 -->
        <list-body :sourceList="hotlist">
          <div>热门榜单</div>
        </list-body>
        <!-- 其他榜单 -->
        <list-body :sourceList="otherList">
          <div>其他榜单</div>
        </list-body>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import ListHeader from '@/components/PageHeader/PageHeader'
import ListBody from './components/AllListBody'
import { getAllWeb } from '../../request/api'
export default {
  name: 'AllList',
  components: {
    ListHeader,
    ListBody
  },
  data () {
    return {
      allList: [],
      hotlist: [],
      mineList: [],
      otherList: [],
      options: {
        scrollY: true,
        scrollX: false,
        mouseWheel: true,
        click: true,
        taps: true
      }
    }
  },
  created () {
    // options是better-scroll配置参数
    this.$nextTick(() => {
      this.scroll = new Bscroll(this.$refs.listContant, this.options)
    })

    // 获取列表数据
    getAllWeb(localStorage.username).then(res => {
      console.log(res)
      // this.allList = res.data
      this.classify(res.data)
    })
  },
  methods: {
    // 将网站分类
    classify (alldata) {
      alldata.forEach(val => {
        if (val.status) {
          this.mineList.push(val)
        } else if (val.tag === 'hot') {
          this.hotlist.push(val)
        } else {
          this.otherList.push(val)
        }
      })
    }
  }
}
</script>

<style>
.wapper {
  position: absolute;
  left: 0;
  right: 0;
  top: 49px;
  bottom: 49px;
  overflow: hidden;
  background: #f6f6f6;
}
</style>



